/**
* Card Theme1
*/
.offerstack-widget {
  background: #e7f6ea;
  padding: 80px 10px;
  text-align: center;
}

.offerstack-widget .offers--wrap {
  position: relative;
  width: 32%;
  height: auto;
  display: inline-block;
  vertical-align: top;
  cursor: pointer;
  border: 3px solid transparent;
  text-align: left;
  margin: 5px 0px;
}

.offerstack-widget .offers--wrap:hover {
  border: 3px solid #4cbb63;
  box-shadow: 0px 4px 14px 0px #71c983;
}

.offerstack-widget .offers--wrap .offer__image {
  width: 100%;
  height: 130px;
  overflow: hidden;
  background: #fff;
  border: 1px solid #a9a8a9;
}

.offerstack-widget .offers--wrap .offer__image img {
  width: 100%;
}

.offerstack-widget .offers--wrap .provider__logo {
  position: absolute;
  left: 20px;
  top: 88px;
  width: 65px;
  height: 65px;
  border: 1px solid #a9a8a9;
  background: #fff;
  padding: 5px;
  vertical-align: middle;
}

.offerstack-widget .offers--wrap .provider__logo img {
  width: 100%;
  margin-top: 15px;
}

.offerstack-widget .offers--wrap .offer--detail {
  padding: 35px 15px 15px 15px;
  height: 300px;
  background: #fff;
  border-right: 1px solid #a9a8a9;
  border-left: 1px solid #a9a8a9;
  overflow: hidden;
}

.offerstack-widget .offers--wrap .offer--detail .offer-title {
  color: #4cbb63;
  font-size: 16px;
  line-height: 18px;
  margin-bottom: 12px;
}

.offerstack-widget .offers--wrap .offer--detail .offer-desc {
  font-size: 14px;
  line-height: 20px;
  color: #686868;
}

.offerstack-widget .offers--wrap .offer--footer {
  width: 100%;
  background: #eff9f1;
  padding: 5px;
  border: 1px solid #a9a8a9;
  padding: 10px;
  text-align: center;
  white-space: nowrap;
}

.offerstack-widget .offers--wrap .offer--footer div {
  text-align: center;
  display: inline-block;
  white-space: nowrap;
}

.offerstack-widget .offers--wrap .offer--footer .price {
  color: #8d8d8d;
  font-size: 12px;
}

.offerstack-widget .offers--wrap .offer--footer .price .amount {
  color: #4cbb63;
  font-weight: 400;
  font-size: 16px;
}

.offerstack-widget .offers--wrap .offer--footer .detail-btn {
  background: #4cbb63;
  color: #fff;
  font-size: 12px;
  padding: 0px 10px;
  margin-left: 1%;
}

.offerstack-widget .offers--wrap .ribbon {
  position: absolute;
  left: -5px;
  top: 10px;
  background: #4cbb63;
  color: #fff;
  width: 60px;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
}

.offerstack-widget .offers--wrap .ribbon:after {
  content: "";
  position: absolute;
  top: 0px;
  right: -15px;
  display: block;
  border: 10px solid #4cbb63;
  border-right-color: transparent;
}

.offerstack-widget .offers--wrap .ribbon:before {
  content: '';
  position: absolute;
  left: 0;
  top: -6px;
  width: 0;
  height: 0;
  border: 3px solid #4cbb63;
  border-top-color: transparent;
  border-left-color: transparent;
  white-space: normal;
}

@media only screen and (max-width: 700px) {
  .offerstack-widget .offers--wrap {
    width: 100%;
  }
  .offerstack-widget .offers--wrap .offer__image {
    width: 100px;
    height: 100px;
    border: 1px solid #a9a8a9;
    float: left;
    display: inline-block;
    vertical-align: top;
    text-align: center;
    padding: 18px;
  }
  .offerstack-widget .offers--wrap .offer__image img {
    width: auto;
    height: 100%;
  }
  .offerstack-widget .offers--wrap .offer--detail-wrap {
    width: calc(100% - 100px);
    display: inline-block;
    height: auto;
  }
  .offerstack-widget .offers--wrap .offer--detail {
    width: 100%;
    height: 59px;
    padding: 10px 10px;
    border-top: 1px solid #a9a8a9;
    border-left: none;
  }
  .offerstack-widget .offers--wrap .offer--detail .offer-title {
    font-size: 14px;
    line-height: 16px;
    margin: 0px;
  }
  .offerstack-widget .offers--wrap .offer--footer {
    width: 100%;
    padding: 5px 10px;
    text-align: right;
    border-left: none;
  }
  .offerstack-widget .offers--wrap .offer--footer .detail-btn {
    font-size: 12px;
  }
  .offerstack-widget .offers--wrap .offer--footer .price {
    float: left;
  }
  .offerstack-widget .offers--wrap .offer--footer .price .amount {
    font-size: 12px;
  }
  .offerstack-widget .offers--wrap .ribbon {
    width: 40px;
    font-size: 12px;
  }
  .offerstack-widget .provider__logo {
    display: none;
  }
  .offerstack-widget .offer--detail .offer-desc {
    display: none;
  }
}

@media only screen and (max-width: 300px) {
  .offerstack-widget .offers--wrap .offer__image {
    width: 100%;
    text-align: center;
    border: 1px solid #a9a8a9;
    border-bottom: none;
  }
  .offerstack-widget .offers--wrap .offer__image img {
    width: 100%;
    height: auto;
  }
  .offerstack-widget .offers--wrap .offer--detail-wrap {
    width: 100%;
  }
}

.offerstack-widget.offerstack-widget-sidebar {
