.offerstack-widget.offerstack-slider {
  padding: 0px;
}

.offerstack-widget.offerstack-slider .offer-nested-slides {
  display: none;
}

.offerstack-widget.offerstack-slider .offer-slide-hide {
  display: none;
}

.offerstack-widget.offerstack-slider .offer-slide-show {
  display: inline-block;
}

.offerstack-widget .offer-pagination {
  display: block;
  text-align: center;
}

.offerstack-widget .offer-pagination li.offer-slide-no {
  display: inline-block;
  text-decoration: none;
  width: 10px;
  height: 10px;
  background: #41ac58;
  border-radius: 50%;
  margin: 15px 1px !important;
  cursor: pointer;
}

.offerstack-widget .offer-pagination li.offer-slide-no.selected-slide {
  background: gray;
}
